<template>

    <!-- 메인 페이지 -->
    <div class="ms-hbd-main" v-if="nowPage == 0">
        <div>
            <img :src="require('@/mimi/hbd.svg')" />
        </div>
        <div class="ms-hbd-en">
            Happy Birthday, MJ!
        </div>
        <div>
            민지야, 생일 축하해!
        </div>
        <div >
            <img @click="nowPage = 1" :src="require('@/mimi/next.svg')" style="width: 150px;" class="ms-main-next-click" />
        </div>
    </div>

    <!-- 케익에 초 불기 -->
    <div v-if="nowPage == 1">
        <img :src="require('@/mimi/cake.png')" style="width: 500px; margin: 50px;" />

        <div class="ms-cake-box">
            <span>{{text[nowText]}}</span>
        </div>

        <img :src="require('@/mimi/next.svg')" v-if="changeCyberMJ == false" style="width: 150px;"
            class="ms-main-next-click" @click="if(nowText < 21) {
               nowText++;
            }
            else {
               changeCyberMJ = true;
            }" />
        <img :src="require('@/mimi/CyberMJ.png')" style="width: 400px;" v-if="changeCyberMJ == true"
            class="ms-cake-change-MJ" @click="nowPage = 2;" />

    </div>

    <!-- 사이버 민지가 되어 케익에 초 불기 -->
    <div v-if="nowPage == 2">
        <inline style="float: left;">
            <br/><br/><br/><br/>
            <div>
                <div>
                    <video style="margin-left: 70px;" class="ms-video" ref="video" id="video" width="700" height="525" autoplay></video>

                    <!-- 사진찍기 버튼 -->
                    <div>
                        <img :src="require('@/mimi/snap.png')"
                        color="info" id="snap" @click="capture()"
                        style="width: 400px;"
                        class="ms-snap"/>
                    </div>
                </div>
            </div>
        </inline>

        <inline>
            <img :src="require('@/mimi/cake.png')" style="width: 500px; margin: 100px; float:left;" />

            <div class="ms-cake-say">
                {{text2[nowText2]}}
            </div>

            <div>
                <img :src="require('@/mimi/next.svg')"
                @click="if(nowText2 < 11){
                    nowText2++
                }
                else {
                    nowPage = 3;
                }"
                style="width: 100px; margin-left: -110px;"
                class="ms-main-next-click"/>
            </div>
        </inline>
    </div>

    <div v-if="nowPage == 2 || nowPage == 3"
    :class = "{ 'ms-video-after' : nowPage == 3 }">
        <!-- 찍힌 사진 -->
        <canvas class="ms-video" ref="canvas" id="canvas" width="400" height="400"></canvas>
    </div>

    <!-- 케이크와의 작별인사 -->
    <div v-if="nowPage == 3">
        <!-- <div style="width: 500px; height: 500px; background-color: gray;">
        </div> -->
        <img :src="require('@/mimi/cake.png')"
    style="width: 300px; float: left; margin: 80px; margin-left: 0px;"/>

        <img :src="require('@/mimi/hbdMJ.png')"/>

        <div class="ms-cake-bye">
            {{text3[nowText3]}}
        </div>

        <div style="clear:both"></div>

        <img class="ms-main-next-click" :src="require('@/mimi/next.svg')"
        style="margin-left: 800px; width: 140px;"
        @click="if(nowText3 < 18) nowText3++;"/>
    </div>

</template>
  
<script>

export default {
    name: 'MS',
    components: {
    },
    data() {
        return {
            video: {},
            canvas: {},
            captures: [],
            /*
            0 : 메인 페이지
            1 : 케익에 초 불기
            2 : 기념사진 찍기
            */
            nowPage: 0,
            nowText: 0, // 0 ~ 21
            text: [
                "안녕! 갑작스럽겠지만, 나는 민지를 위한 사이버 생일 케이크야. 어서 인사하렴!",
                "음...",
                "...",
                "... ...",
                "... ... ...",
                "뭐라고? 열심히 입김을 불고 있었어?",
                "벌써 초의 불을 끄고 싶었던 거야?",
                "후훗 ^_^ 민지는 정말 귀엽구나!",
                "얼른 촛불을 꺼봐도 좋아! 기다려줄게!",
                "...",
                "... ...",
                "... ... ...",
                "어라...",
                "입김 불고있는 거 맞아?",
                "흠...",
                "앗! 이유를 알았어!",
                "놀랍게도 난 액정 속 케이크라서 입김을 느끼지 못하는 것 같아...",
                "혹시... 많이 모자란 사이버 케이크인 나라도 괜찮다면...",
                "너도 사이버 민지가 되어서 입김을 불어보는 건 어때?",
                "무슨 헛소리냐고?",
                "아냐, 내 생각엔 이 방법이라면 진짜 효과가 있을 거야!",
                "어서 해보자!"
            ],
            changeCyberMJ: false,
            nowText2: 0, // 0 ~ 11
            text2: [
                "안녕! 오랜만이야!",
                "이제 사이버 민지가 되었으니...",
                "이번엔 입김을 힘껏 불어주겠니?",
                "더이상 촛불을 냅뒀다가는",
                "촛농 범벅 케이크를 먹게될지도 모른다구!",
                "어서 입김을 불어줘!",
                "아 참!",
                "내 쪽으로 입김을 부는 사진을 꼭 찍어줄 수 있을까?",
                "제발 부탁이야! 나, 케이크 평생의 소원!",
                "후~ 불면서 '사이버 사진 촬영' 버튼을 눌러줘",
                "찍은 사진은 이 텍스트 바로 밑에서 확인할 수 있어",
                "사진을 다 찍었다면, 아래 버튼을 눌러줘!"
            ],
            nowText3: 0, // 0 ~ 18
            text3: [
                "입김을 불어줘서 정말 고마워!",
                "왼쪽 이미지는 기념샷으로 가지도록 해 ㅎㅎ",
                "정말 아쉽게도 내 촛불이 꺼지지 않았어..",
                "아마 우리 둘 다 사이버 세상 속이어서 그런가봐!",
                "다 소용없는 짓이었지만",
                "그래도 재밌었으면 된 거 아닐까?",
                "아.. 아니라구?",
                "미안해!",
                "하지만 사이버 민지가 되어서 입김을 불어보는 경험도",
                "나름 괜찮지 않았니?",
                "ㅎㅎ 그렇게 믿고 있을게!",
                "위 사진은 지안이가 정성스레 만든 생일 축하 이미지야",
                "열심히 만들었으니 열심히 감상하도록 해!",
                "그럼 사이버 케이크는 이만 사라지도록 할게",
                "현실 세상에서는 케이크 촛불을 꼭 불도록 하렴!",
                "진짜 갈게",
                "생일 정말 축하해!",
                "맛있는 거 많이 먹고 행복해야해!",
                "뿅!"
            ]
        }
    },
    updated() {
        if(this.nowPage >= 2){
            console.log("2페이지");
            let recaptchaScript = document.createElement('script');
            recaptchaScript.setAttribute('src', "https://cdn.jsdelivr.net/npm/vue/dist/vue.js");
            document.head.appendChild(recaptchaScript);
                this.video = this.$refs.video
                if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
                    navigator.mediaDevices.getUserMedia({ video: true }).then(stream => {
                        this.video.srcObject = stream
                        this.video.play()
                        console.log("고고");
                    })
                }
        }

    },
    methods: {
        capture() {
            console.log("사진찍었어!");
            this.canvas = this.$refs.canvas
            this.canvas.getContext('2d').drawImage(this.video, 0, 0, 500, 500)
            this.captures.push(this.canvas.toDataURL('image/png'))
            console.log(this.captures)
        },
    },
}
</script>
  
<style>
@import url(https://fonts.googleapis.com/earlyaccess/notosanskr.css);
@import url(https://fonts.googleapis.com/earlyaccess/notosans.css);

body {
    font-family: 'Noto Sans KR';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;

    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}

.ms-video-after {
    /* display: inline; */
    float:left;
}

.ms-hbd-main {
    letter-spacing: -10px;
    font-size: 160px;
    font-weight: 600;

    position: relative;
    animation: fadeInUp 1s;
}

.ms-hbd-en {
    font-family: 'Noto Sans';
}

.ms-main-next-click:hover {
    filter: brightness(75%);
    cursor: pointer;
}

.ms-cake-change-MJ:hover {
    filter: brightness(75%);
    cursor: pointer;
}

.ms-snap:hover {
    filter: brightness(75%);
    cursor: pointer;
}

::-webkit-scrollbar {
    display: none;
    /* Chrome , Safari , Opera */
}

.ms-video {
    transform: rotateY(180deg);
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);

    border-radius: 50px;
}

.ms-cake-say {
    margin-bottom: 50px;
    letter-spacing: -4px;
    font-size: 33px;
    font-weight: 700;
    padding: 120px;
    padding-top: 250px;
    padding-bottom: 0px;
}

.ms-cake-bye {
    margin-bottom: 50px;
    letter-spacing: -4px;
    font-size: 33px;
    font-weight: 700;
    text-align: center;
    margin-left: 800px;
    margin-top: 30px;
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }

    to {
        opacity: 1;
        transform: translateZ(0);
    }
}

.ms-cake-box {
    margin-bottom: 50px;
    letter-spacing: -5px;
    font-size: 40px;
    font-weight: 700;
}
</style>

  